.breadcrumbs {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .breadcrumbs li {
    display: flex;
    align-items: center;
  }
  
  .breadcrumbs li a {
    text-decoration: none;
    color: blue;
  }
  
  .breadcrumbs li a:hover {
    text-decoration: underline;
  }
  
  .breadcrumbs li span.separator {
    margin: 0 8px;
    color: gray;
  }
  
  .breadcrumbs li:last-child a {
    pointer-events: none;
    color: black;
    font-weight: bold;
  }
